body, html, #root {
  height: 100%;
  margin: 0;
}
.backgroundCard {
  height: 90%;
  border: .125rem solid black;
  border-radius: 25px;
  padding: 1rem!important;
  background-color: rgba(87,162,182, .7);
}
.holder {
  position: relative;
}

.images {
  width: 200px;
  height: 150px;
  margin-right: 5px;
  opacity: 1;
  -webkit-transition: .5s ease;
  transition: .5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.git {
  height: 70px;
  width: 70px;
}
.row {
  min-width: 341px;
}

.col {
  min-width: 341px;
}

.project {
  height: 90%;
  margin: auto;
  margin-top: 15px;
  margin-bottom: 15px;
  /* max-width: 350px; */
  border: .125rem solid black;
  border-radius: 25px;
  padding: 1rem!important;
  background-color: rgba(87,162,182, .7);
}

a:hover .images, a:hover .git {
  opacity: 0.4;
}

a:hover .middleText, a:hover .gitHover {
  opacity: 1;
}

.middleText {
  -webkit-transition: .5s ease;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.text {
  background-color: rgb(87, 162, 182);
  color: black;
  font-weight: bold;
  font-size: 16px;
  padding: 10px 20px;
}

.gitHover {
  -webkit-transition: .5s ease;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.gitText {
  background-color: rgb(0, 0, 0);
  color: white;
  font-weight: bold;
  font-size: 16px;
  padding: 12px 8px;
}
.gradient {
  /* background: -moz-linear-gradient(90deg, rgba(89, 91, 255, 0.7), rgba(84, 232, 108, 0.7) 100%);
  background: -webkit-linear-gradient(90deg, rgba(89, 91, 255, 0.7), rgba(84, 232, 108, 0.7) 100%);
  background: -o-linear-gradient(90deg,  rgba(89, 91, 255, 0.7), rgba(84, 232, 108, 0.7) 100%); */
  background: -webkit-gradient(linear,  left bottom, left top,  from(rgba(89, 91, 255, 0.7)), to(rgba(84, 232, 108, 0.7)));
  background: linear-gradient(0deg,  rgba(89, 91, 255, 0.7), rgba(84, 232, 108, 0.7) 100%);
}
